popcornyellow: rgb(255, 230, 149)
*/

:root {
  --modalheight: 82vh;
  --modalwidth: 85vw;
}

.movie-card {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 6vh 0px;
  z-index: 10;
  position: fixed;
  width: 100%;
  height: 100%;
  font-family: var(--fontfamily);
  color: var(--midgrey);
  background-color: rgba(35, 38, 46, 0.6);
}

/* ======================================================= */
/* Backdrop */

.movie-card .backdrop {
  border-radius: 20px;
  height: 82vh;
  height: var(--modalheight);
  width: 85vw;
  width: var(--modalwidth);
  position: absolute;
  z-index: -1;
  overflow: hidden;
  background-color: var(--purple);
}

.movie-card .backdrop img {
  width: 100%;
  z-index: -2;
  border-radius: 20px;
  position: absolute;
}

.movie-card .backdrop .overlay {
  background-color: var(--darkgrey);
  opacity: 0.5;
  height: 100%;
  width: 100%;
  position: absolute;
}

/* ======================================================= */
/* ======================================================= */
/* Info section */

.movie-card .info {
  border-radius: 20px;
  background: linear-gradient(to top, var(--offwhite) 50%, transparent 100%);
  position: absolute;
  height: 82vh;
  height: var(--modalheight);
  width: 85vw;
  width: var(--modalwidth);
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

/* ======================================================= */
/* Info Heading */

.movie-card .info .heading {
  margin-top: 13%;
  position: relative;
  padding: 0px 25px;
  height: 30%;
  overflow: hidden;
}

.movie-card .heading .poster {
  position: relative;
  float: left;
  margin-right: 20px;
  height: 100%;
  box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.5);
}

.movie-card .heading h1 {
  color: var(--darkgrey);
  font-weight: 500;
  margin: 0;
  font-size: 3vw;
}

.movie-card .heading h4 {
  font-weight: 400;
  margin: 0;
  font-size: 1.8vw;
}

.movie-card .heading .genres {
  display: inline-block;
  margin-left: 10px;
  color: var(--lightgrey);
  font-size: 2vw;
}

.movie-card .heading .runtime {
  display: inline-block;
  /* margin-top: 15px; */
  padding: 5px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 1.7vw;
}

/* ======================================================= */
/* Info Description */

.movie-card .description {
  color: var(--darkgrey);
  padding: 15px 25px;
  overflow: hidden;
  height: 40%;
}

.movie-card .description p {
  margin: 0;
  font-size: 1.8vw;
}

/* ======================================================= */
/* Info Ratings */

.movie-card #line2 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 1.4em;
  margin: 5px 0px;
}

.movie-card #ratings {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-left: 15px;
  font-size: 2.8vh;
  overflow: hidden;
}

.movie-card #ratings li {
  list-style: none;
  padding: 0px 5px;
}

.movie-card #ratings li img {
  height: 1.5vw;
  margin-right: 4px;
}

.movie-card #ratings li a {
  text-decoration: none;
  color: var(--darkgrey);
  font-size: 1.5vw;
  display: -webkit-flex;
  display: flex;
}

/* ======================================================= */
/* Info Streams */

.movie-card #streams {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 12%;
  margin: 5px 25px;
}

.movie-card #streams h3 {
  margin: 0;
  font-size: 2.5vh;
}

.movie-card #streams ul {
  margin: 0;
  margin-left: 10px;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
  height: 100%;
}

.movie-card #streams li {
  margin: 3px 10px;
}

.movie-card #streams li a {
  -webkit-align-items: baseline;
          align-items: baseline;
  display: -webkit-flex;
  display: flex;
  height: 100%;
}

.movie-card #streams li img {
  height: 18px;
  /* width: 18px; */
}

