  fill: var(--darkgrey);
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
  color: var(--offwhite);
  padding: 0.8em;
  text-decoration: none;
  outline: none;
  font-size: 20px;
}

.bm-item:hover {
  color: var(--purple);
}

.bm-item:active {
  color: var(--purple);
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
  position: relative;
  top: 0;
  left: 0;
}

.bm-div {
  padding: 0;
}
