.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input[type=checkbox] {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch input[type=checkbox]:checked + .slider {
  background: green;
}

.switch input[type=checkbox]:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

.slider {
  border-radius: 34px;
  box-sizing: border-box;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--red);
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  left: 4px;
  bottom: 4px;
  width: 26px;
  height: 26px;
  background-color: white;
  transition: .4s;
  -webkit-transition: .4s;
  border-radius: 50%;
}

